
.title {
  width: 100%;
  height: 8rem;
  overflow: hidden;
  position: relative;
}
.title div {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0.4;
    position: absolute;
    background-color: #5B7492;
}
.title img {
    top: 50%;
    width: 100%;
    position: relative;
    -webkit-transform: translate(0, -50%);
            transform: translate(0, -50%);
}
.title h3 {
    width: 70%;
    color: #fff;
    margin: 0;
    font-size: 0.5rem;
    line-height: 1rem;
    right: 5%;
    bottom: 1.6rem;
    text-align: right;
    position: absolute;
    text-shadow: 1px 1px 10px rgba(0, 0, 0, 0.5);
}
.title h3:before {
      content: "";
      width: 3rem;
      bottom: 1.2rem;
      right: 0;
      display: block;
      position: absolute;
      height: 4px;
      background: linear-gradient(90deg, #a1c4fd 0%, #c2e9fb 95%);
}
.title h3 p {
      margin: 0;
}
.title h3 p:first-child {
      margin-bottom: .5rem;
}
.list {
  width: 90%;
  z-index: 1;
  position: relative;
  margin: .3rem auto 0;
}
.list .listCon {
    background: #FFFFFF;
    box-shadow: 0 2px 4px 0 rgba(142, 197, 252, 0.3);
    border-radius: 12px;
    padding: 0.3rem;
    margin: 0.4rem auto 0.4rem;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.list .listCon img {
      width: 2rem;
      min-width: 2rem;
      margin-right: 0.4rem;
}
.list .listCon p {
      font-size: 0.4rem;
      color: #5B7492;
}



