
.fade-enter-active,
.fade-leave-active {
  transition: opacity .5s;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}
.app-view .swiper-container {
  width: 100%;
}
.app-view .swiper-slide {
  height: 8rem;
  overflow: hidden;
  position: relative;
}
.app-view .swiper-container-horizontal > .swiper-pagination-bullets {
  bottom: 1rem;
  width: 95%;
  text-align: right;
}
.swiper-slide div {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.4;
  position: absolute;
  background-color: #5B7492;
}
.swiper-slide img {
  top: 50%;
  width: 100%;
  position: relative;
  -webkit-transform: translate(0, -50%);
          transform: translate(0, -50%);
}
.swiper-slide h3 {
  width: 70%;
  color: #fff;
  margin: 0;
  font-size: 0.5rem;
  line-height: 1rem;
  right: 5%;
  bottom: 2.6rem;
  text-align: right;
  position: absolute;
  text-shadow: 1px 1px 10px rgba(0, 0, 0, 0.5);
}
.swiper-slide h3:before {
    content: "";
    width: 3rem;
    bottom: -0.6rem;
    right: 0;
    display: block;
    position: absolute;
    height: 4px;
    background: linear-gradient(90deg, #a1c4fd 0%, #c2e9fb 95%);
}
.swiper-pagination-bullet-active {
  background: #fff;
}
.list {
  width: 90%;
  z-index: 1;
  position: relative;
  margin: .3rem auto 0;
}
.list .listTime {
    width: 100%;
    height: 1rem;
    text-align: center;
    line-height: 1rem;
    font-size: 0.5rem;
    background-image: linear-gradient(-180deg, #E0C3FC 0%, #8EC5FC 100%);
    box-shadow: 0 1px 5px 0 #8EC5FC;
    border-radius: 100px;
    color: #FFFFFF;
    margin-top: 0.4rem;
}
.list .listCon {
    background: #FFFFFF;
    box-shadow: 0 2px 4px 0 rgba(142, 197, 252, 0.3);
    border-radius: 12px;
    padding: 0.3rem;
    margin-top: 0.4rem;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.list .listCon img {
      width: 2rem;
      min-width: 2rem;
      margin-right: 0.4rem;
}
.list .listCon p {
      font-size: 0.4rem;
      color: #5B7492;
}
.list + .pacman {
    margin-top: 0.5rem;
}
.moreButton {
  width: 90%;
  height: 1rem;
  text-align: center;
  line-height: 1rem;
  font-size: 0.45rem;
  background-image: linear-gradient(-180deg, #E0C3FC 0%, #8EC5FC 100%);
  box-shadow: 0 1px 5px 0 #8EC5FC;
  border-radius: 100px;
  color: #FFFFFF;
  margin: .4rem auto 0.4rem;
  border: none;
  display: block;
}



