
.content-page {
  width: 100%;
  height: 100%;
  overflow: auto;
}
.content-page .contentImg {
    height: 6rem;
    overflow: hidden;
    position: relative;
}
.content-page .contentImg img {
      top: 50%;
      width: 100%;
      position: relative;
      -webkit-transform: translate(0, -50%);
              transform: translate(0, -50%);
      -webkit-filter: blur(4px);
              filter: blur(4px);
}
.content-page .contentImg p {
      width: 70%;
      margin: 0;
      padding: 0;
      right: 5%;
      bottom: 1rem;
      text-align: right;
      font-size: 0.5rem;
      color: rgba(255, 255, 255, 0.9);
      position: absolute;
      text-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
}
.content-page .contentBody {
    color: #666666;
    padding: 5%;
    position: relative;
}
.content-page .contentBody h2,
    .content-page .contentBody li,
    .content-page .contentBody p {
      text-align: justify !important;
}
.content-page .contentBody h2 {
      color: #606d80;
      position: relative;
      z-index: 1;
}
.content-page .contentBody a {
      color: #8EC5FC;
      word-break: break-word;
}
.content-page .contentBody img {
      width: 100%;
}
.content-page .contentBody p {
      font-size: .45rem;
}
.content-page .contentBody .headline-title {
      color: #3761bd;
}
.content-page .contentBody .meta {
      text-align: left;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
}
.content-page .contentBody .meta .avatar {
        vertical-align: middle;
        display: inline-block;
        width: 0.6rem;
        margin-right: 0.1rem;
}
.content-page .contentBody .meta span {
        color: #666;
}
.content-page .contentBody .meta .author {
        color: #8EC5FC;
        display: inline-table;
}
.content-page .contentBody .content {
      font-size: 0.35rem;
}
.content-page .contentBody .question blockquote {
      text-align: justify;
      position: relative;
}
.content-page .contentBody .question blockquote::before {
        content: '';
        left: -.5rem;
        display: block;
        height: 100%;
        border-left: .1rem solid #8EC5FC;
        position: absolute;
}
.content-page .contentBody .question:first-child {
      padding-top: 0;
}
.content-page .contentBody .question:last-child {
      padding-bottom: 0;
}
.content-page .contentBody .question:last-child::before {
        display: none;
}
.content-page .contentBody .question ul {
      padding-left: 0.3rem;
}
.content-page .contentBody .question ul li {
        text-align: left;
}
.content-page .contentBody .content-image {
      max-width: 100%;
}
.content-page .contentBody .view-more {
      text-align: center;
      margin-bottom: .5rem;
}
.content-page .contentBody .view-more a {
        z-index: 1;
        position: relative;
}



